.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.glide * {
  box-sizing: inherit;
}

.glide__track {
  overflow: hidden;
}

.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform;
}

.glide__slides--dragging {
  user-select: none;
}

.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: initial;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide--rtl {
  direction: rtl;
}

@font-face {
  font-family: 'Clear Sans';
  src: local("Clear Sans Regular"), local("ClearSans"), url("../fonts/ClearSans.woff2") format("woff2"), url("../fonts/ClearSans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Clear Sans';
  src: local("Clear Sans Bold"), local("ClearSans-Bold"), url("../fonts/ClearSans-Bold.woff2") format("woff2"), url("../fonts/ClearSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

html {
  background: #fff;
  font-family: "Clear Sans", Arial, Helvetica, sans-serif;
  font-size: 1rem;
  line-height: 1.123;
  color: #4a4a4a;
  cursor: default;
}

html,
body {
  min-height: 100%;
}

:root {
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -o-text-size-adjust: none;
  text-size-adjust: none;
}

header,
footer,
section,
article,
aside,
main,
div,
p,
button,
form,
input,
textarea,
label,
ol,
ul,
li,
a {
  box-sizing: border-box;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

input,
textarea,
label {
  display: block;
}

input,
textarea {
  border-radius: 0;
}

input::-ms-clear,
textarea::-ms-clear {
  display: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  box-sizing: border-box;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

p {
  margin: 0 0 1em 0;
}

img {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: auto;
  max-height: 100%;
}

svg {
  display: block;
  pointer-events: none;
}

button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  border: 0;
  border-radius: 0;
  color: inherit;
  background: rgba(0, 0, 0, 0);
  cursor: pointer;
}

a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

a:hover, a:active,
button:hover,
button:active {
  outline: none;
}

body.isFixed {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

@media screen and (min-width: 1280px) {
  body.isFixed {
    position: static;
    overflow: visible;
  }
}

.app {
  overflow: hidden;
}

.section {
  min-height: 100vh;
  padding: 3.125em 0;
  text-align: center;
}

.section p,
.section ul {
  line-height: 1.75;
}

.section--bg {
  background-color: #f7f7f7;
}

@media screen and (min-width: 768px) {
  .section {
    min-height: 31.25em;
    padding-top: 4.375em;
    padding-bottom: 4.375em;
  }
}

@media screen and (min-width: 1280px) {
  .section {
    margin-left: 15.625em;
    padding-top: 2.5em;
    padding-bottom: 2.5em;
  }
}

@media screen and (min-width: 1440px) {
  .section {
    padding-top: 10vh;
    padding-bottom: 10vh;
  }
}

@media screen and (min-width: 1900px) {
  .section {
    margin-left: 21.875em;
  }
}

.container {
  max-width: 84.375em;
  margin: 0 auto;
  padding: 0 1.5625em;
}

@media screen and (min-width: 768px) {
  .container {
    padding-left: 3.125em;
    padding-right: 3.125em;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    padding-left: 4.375em;
    padding-right: 4.375em;
  }
}

@media screen and (min-width: 1600px) {
  .container {
    padding-left: 6.25em;
    padding-right: 6.25em;
  }
}

.title {
  display: inline-block;
  margin-bottom: 0.9375em;
  padding-bottom: 0.3125em;
  font-size: 1.5em;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 2px solid #0176c0;
}

@media screen and (min-width: 1280px) {
  .title {
    font-size: 2.25em;
  }
}

.burger {
  position: absolute;
  top: 50%;
  left: 1.5625em;
  height: 2.8125em;
  width: 2.5em;
  z-index: 1;
  transform: translate(0, -50%);
  pointer-events: auto;
}

.burger:hover, .burger:active, .burger:focus {
  outline: none;
}

@media screen and (min-width: 1280px) {
  .burger {
    opacity: 0;
    visibility: hidden;
    transition: none;
  }
}

.burger.isActive .burger__line {
  background-color: rgba(0, 0, 0, 0);
}

.burger.isActive .burger__line::before {
  top: 0;
  transform: rotateZ(-45deg);
  transition: top 0.2s cubic-bezier(0.23, 1, 0.32, 1), transform 0.2s cubic-bezier(0.23, 1, 0.32, 1) 0.2s;
}

.burger.isActive .burger__line::after {
  top: 0;
  transform: rotateZ(45deg);
  transition: top 0.2s cubic-bezier(0.23, 1, 0.32, 1), transform 0.2s cubic-bezier(0.23, 1, 0.32, 1) 0.2s;
}

.burger__line {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  height: 4px;
  width: 100%;
  transform: translateY(-50%);
  background-color: #f56b36;
  transition: all 0.2s ease;
}

.burger__line::before, .burger__line::after {
  content: '';
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: inherit;
  background-color: #f56b36;
  transform: translate(0, 0) rotateZ(0deg);
  transform-origin: 50% 50%;
  transition: top 0.2s cubic-bezier(0.23, 1, 0.32, 1) 0.2s, transform 0.2s cubic-bezier(0.23, 1, 0.32, 1) 0s, background-color 0.4s ease;
}

.burger__line::before {
  top: -0.6875em;
}

.burger__line::after {
  top: 0.6875em;
}

.logo {
  display: block;
  height: 2.625em;
  width: 8.75em;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.nav {
  height: 100%;
  width: 100%;
  overflow: auto;
}

@media screen and (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media screen and (min-width: 1280px) {
  .nav {
    justify-content: flex-start;
    align-items: flex-start;
    overflow: hidden;
  }
}

.nav__list {
  display: flex;
  flex-direction: column;
  flex-direction: flex-start;
  align-items: center;
  justify-content: center;
  padding-bottom: 1.875em;
}

@media screen and (min-width: 1280px) {
  .nav__list {
    align-items: flex-start;
  }
}

.nav__item {
  margin-top: 10vmin;
}

@media screen and (min-width: 768px) {
  .nav__item {
    margin-top: 2.5em;
  }
}

@media screen and (min-width: 1440px) {
  .nav__item {
    margin-top: 6vh;
  }
}

.nav__link {
  position: relative;
  padding-bottom: 0.3125em;
  font-size: 1.25em;
  color: #4a4a4a;
  text-decoration: none;
}

.nav__link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #0176c0;
  transform: scaleX(0);
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.nav__link.isCurrent::after {
  transform: scaleX(1);
}

@media screen and (min-width: 1280px) {
  .nav__link:hover::after {
    transform: scaleX(1);
  }
}

@media screen and (min-width: 1900px) {
  .nav__link {
    font-size: 1.5em;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  height: 4.375em;
  width: 100%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  will-change: transform;
  padding: 0 1.5625em;
  pointer-events: none;
}

.header.isScrolled .header__logo {
  opacity: 0;
  visibility: hidden;
}

@media screen and (min-width: 1280px) {
  .header {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
    width: 15.625em;
    padding-top: 1.25em;
    background-color: #fff;
    box-shadow: 2px 0 30px -5px rgba(0, 0, 0, 0.3);
  }
  .header.isScrolled .header__logo {
    opacity: 1;
    visibility: visible;
  }
}

@media screen and (min-width: 1440px) {
  .header {
    padding-top: 6vh;
  }
}

@media screen and (min-width: 1900px) {
  .header {
    width: 21.875em;
    padding-left: 3.125em;
  }
}

.header__wrap {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: 0;
  padding-top: 4.375em;
  background-color: #fff;
  transform: translateX(-105%);
  transition: transform 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  pointer-events: auto;
}

.header__wrap.isOpened {
  transform: translateX(0);
}

@media screen and (min-width: 1280px) {
  .header__wrap {
    position: static;
    padding-top: 0;
    transform: translateX(0);
  }
}

.header__logo {
  position: relative;
  z-index: 1;
  pointer-events: auto;
}

@media screen and (min-width: 1900px) {
  .header__logo {
    height: 3.15em;
    width: 10.5em;
  }
}

.hero {
  position: relative;
  height: 100vh;
  padding: 0;
}

.hero__slide {
  height: 100vh;
  width: 100vw;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.625em;
  background-position: 50% 50%;
  background-size: auto 100%;
  background-repeat: no-repeat;
  overflow: hidden;
}

.hero__slide::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.35);
}

@media screen and (min-width: 1024px) {
  .hero__slide {
    background-size: cover;
  }
}

.hero__title {
  position: relative;
  z-index: 1;
  font-size: 7vw;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.01em;
  color: #fff;
}

@media screen and (min-width: 1024px) {
  .hero__title {
    max-width: 10.625em;
    font-size: 5em;
  }
}

.hero__subtitle {
  position: relative;
  z-index: 1;
  max-width: 18.75em;
  margin-top: 0.625em;
  font-size: 5vw;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  line-height: 1.25;
}

@media screen and (min-width: 1024px) {
  .hero__subtitle {
    font-size: 2.5em;
  }
}

@media screen and (min-width: 1280px) {
  .hero .glide {
    width: calc(100% - 15.625em);
    margin-left: 15.625em;
  }
}

@media screen and (min-width: 1900px) {
  .hero .glide {
    width: calc(100% - 21.875em);
    margin-left: 21.875em;
  }
}

.hero .glide__arrow {
  position: absolute;
  top: 50%;
  height: 2.5em;
  width: 2.5em;
  transform: translate(0, -50%);
}

.hero .glide__arrow:focus {
  outline: none;
}

.hero .glide__arrow svg {
  height: 100%;
  width: 100%;
  fill: #fff;
  transition: fill 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.hero .glide__arrow--right {
  right: -0.3125em;
}

.hero .glide__arrow--right svg {
  transform: rotateZ(180deg);
}

.hero .glide__arrow--left {
  left: -0.3125em;
}

@media screen and (min-width: 768px) {
  .hero .glide__arrow {
    height: 3.75em;
    width: 3.75em;
  }
  .hero .glide__arrow--right {
    right: 0;
  }
  .hero .glide__arrow--left {
    left: 0;
  }
}

@media screen and (min-width: 1280px) {
  .hero .glide__arrow:hover svg {
    fill: #f56b36;
  }
}

.hero .glide__bullets {
  position: absolute;
  bottom: 0.625em;
  left: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(-50%, 0);
}

.hero .glide__bullet {
  height: 0.9375em;
  width: 0.9375em;
  margin: 0.5em;
  border-radius: 50%;
  background-color: #fff;
  transition: all cubic-bezier(0.23, 1, 0.32, 1) 0.4s;
}

.hero .glide__bullet--active {
  background-color: #f56b36;
}

.about__item {
  margin-top: 0.3125em;
  text-align: left;
}

.about__item span {
  margin-left: 0.3125em;
}

.product {
  height: 100%;
  padding: 1.25em;
  border: 1px solid #ccc;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.product--tall .product__img {
