}

.product--stack .product__wrap {
  flex-direction: column;
}

.product--stack .product__img {
  width: 60%;
  margin-top: 0.625em;
  margin-bottom: 0.3125em;
}

@media screen and (min-width: 1280px) {
  .product:hover {
    border-color: #0176c0;
  }
  .product:hover .product__type {
    border-top-color: #0176c0;
    border-bottom-color: #0176c0;
  }
}

.product__wrap {
  display: flex;
  align-items: center;
  margin: 0;
}

.product__caption {
  width: 13.4375rem;
  margin: 0 auto 0.9375em;
  font-size: 1.25em;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.3;
}

.product__type {
  display: inline-block;
  padding: 0.1875em 0;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  font-weight: normal;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.product__img {
  width: 90%;
  margin: 1.25em auto;
}

.product__desc {
  font-size: 0.875em;
}

.product__link {
  color: #0176c0;
}

.production__text {
  text-align: left;
}

.production__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15em, 1fr));
  grid-gap: 1.25em;
  margin-top: 2.5em;
}

.certificates__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 -1.25em;
}

.certificates__item {
  width: 10.625em;
  margin: 1.25em;
}

.certificates__text {
  text-align: left;
}

.catalogs__list {
  display: grid;
  grid-gap: 1.25em;
  grid-template-columns: repeat(auto-fill, minmax(8.75em, 1fr));
}

.catalogs__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.25em;
  border: 1px solid #ccc;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

@media screen and (min-width: 1280px) {
  .catalogs__item:hover {
    border-color: #0176c0;
  }
}

.catalogs__caption {
  font-size: 1.25em;
  font-weight: normal;
  line-height: 1.2;
}

.catalogs__icon {
  margin: 0.625em 0;
  fill: #0176c0;
}

.catalogs__link {
  color: #0176c0;
}

.leftovers__wrap {
  width: 100%;
  overflow-y: auto;
  box-shadow: 0 0 30px -10px rgba(0, 0, 0, 0.5);
}

.leftovers__table {
  width: 100%;
  min-width: 31.25em;
  font-size: 1em;
  border-collapse: collapse;
}

.leftovers__b-row {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.leftovers__b-row:nth-child(odd) {
  background-color: #f2f2f2;
}

.leftovers__cell {
  width: calc(100% / 5);
  padding: 0.5em 0.1875em;
  font-size: 0.875em;
}

@media screen and (min-width: 1280px) {
  .leftovers__cell {
    font-size: 1em;
    padding: 0.75em 0.5em;
  }
}

.objects__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15em, 1fr));
  grid-gap: 1.25em;
}

.objects__item {
  min-height: 13.4375em;
}

.objects__link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #4a4a4a;
  border: 1px solid #ccc;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

@media screen and (min-width: 1280px) {
  .objects__link:hover {
    border-color: #0176c0;
  }
}

.objects__caption {
  padding: 0.625em;
}

@media screen and (min-width: 1280px) {
  .contacts__row {
    display: flex;
    flex-direction: row-reverse;
  }
}

.contacts__col {
  margin-top: 1.25em;
}

@media screen and (min-width: 1280px) {
  .contacts__col:first-child {
    width: 30%;
    margin-left: 1.875em;
  }
  .contacts__col:last-child {
    width: 70%;
  }
}

.contacts__logo {
  margin: 0 auto;
}

@media screen and (min-width: 1280px) {
  .contacts__logo {
    margin: 0;
  }
}

.contacts__list {
  margin-top: 0.9375em;
}

@media screen and (min-width: 1280px) {
  .contacts__list {
    text-align: left;
  }
}

.contacts__item {
  margin-top: 0.625em;
}

.contacts__link {
  color: #0176c0;
}

.contacts__map {
  height: 50vmax;
}

.contacts__map img {
  max-height: none;
}

@media screen and (min-width: 1280px) {
  .contacts__map {
    height: 70vh;
  }
}
